import React, {useState, createRef} from "react";
import logo from './logo.svg';
import './App.css' ;
import {NavLink, Route} from 'react-router-dom'
import 'materialize-css/dist/css/materialize.min.css'
import Resume from "./purecss/resume";
import list1 from './icon/list.png'
import list2 from './icon/list2.png'
import list3 from './icon/list3.png'
import list4 from './icon/list4.png'
import list5 from './icon/list5.png'
import list6 from './icon/list6.png'
import list7 from './icon/list7.png'
import list8 from './icon/list8.png'
import close from './icon/close.png'
import Materalize from "./materalize/materalize";


const Data = (language) => {
    const uzb = (language === 'uzb')
    const data = {
        fullName: uzb ? 'Ismanov Quandiq' : 'Исманов Куандык',
        profession: 'Frontend Developer',
        info: uzb ? 'Ma\'lumot' : 'Инфо',
        firstName: uzb ? 'Ismanov' : ' Исманов ',
        lastName: uzb ? 'Quandiq' : ' Куандык ',
        father: uzb ? 'Ermurat o\'g\'li' : 'Ермуратович',
        location: 'Tashkent reg. Akhangaran',
        date: '1-aprel 1998',
        contact: uzb ? 'Bog\'lanish' : 'Контакт',
        email: 'ismanov98q@mail.ru',
        github: 'ismanovN1',
        num: '+998  99-868-98-84',
        tg: '@IIIKKK',
        vk: 'Исманов Куандык',
        edu: uzb ? 'Ta\'lim' : 'Образование',
        year1: '2014-2017',
        year2: '2018-2022',
        colage: uzb ? 'Pedagokika va ijtimoiy iqtisodyot kolleji' : 'Экономический и педагогический колледж',
        univer: uzb ? 'Toshkent Axborot Texnologiyalari Universitedi (Masofaviy ta\'lim)' : 'Ташкентский университет информационных технологий (дистанционное обучение)',
        job: uzb ? 'Tajriba' : 'Опыт',
        yearJob: 'avgust 2019',
        job1: uzb ? 'Frilancerlik 4 oy' : 'Фрилансер 4 месяц',
        tech: uzb ? 'Texnologiyalar' : 'Технологии',
        prlang: uzb ? 'Dasturlash tilllari' : 'языки программирования',
        frm: uzb ? 'Frameworklar, Kutubxonalar' : 'Фреймворки,Библиотеки',
        dr: uzb ? 'Boshqalar' : 'Другие',
    }
    return data

}


function App() {

    const [navbar, setNav] = useState('reactLogoLarge')
    const [app, setApp] = useState('none')
    const [show, setShow] = useState('yes')
    const [myImage, setmyImage] = useState('not')
    const [srcMyImage, setSrc] = useState('')
    const [link1, setLink1] = useState('/materalize')
    const [link2, setLink2] = useState('/')
    const [language, setLanguage] = useState('uzb')

    const data = Data(language)

    const showImage = (e) => {

        if (myImage === 'myImage') {
            setmyImage('not')
        } else {
            setSrc(e.target.src)
            setmyImage('myImage')
        }

    }

    return (
        <div className='root'>
            <div className='top'><img src="https://images.vfl.ru/ii_save/1537038138/6fd1bd3b/23351277.png" alt=""/>
            </div>
            <section>
                <div className='leaf1'><img src={list1}/></div>
                <div className='leaf2'><img src={list2}/></div>
                <div className='leaf3'><img src={list3}/></div>
                <div className='leaf4'><img src={list4}/></div>
                <div className='leaf5'><img src={list5}/></div>
                <div className='leaf6'><img src={list6}/></div>
                <div className='leaf7'><img src={list7}/></div>
                <div className='leaf8'><img src={list8}/></div>

            </section>
            <div className={app}></div>
            <Route exact path={link2} render={() => (<Resume showImage={showImage} data={data}/>)}/>
            <Route exact path={link1} render={() => (<Materalize showImage={showImage} data={data}/>)}/>
            <div className={navbar}
                 onClick={() => {
                     setNav('reactLogoLarge')
                     setTimeout(() => {
                         setShow('yes')
                     }, 1000)
                 }}><img className="App-logo" src={logo}/></div>

            <div className={show}>
                <NavLink to='/materalize'>
                    <div className='materalize' onClick={() => {
                        if (link2 === '/') {
                            setNav('reactLogoSmall')
                            setShow('not')
                            setApp('app')
                            setLink2('/materalize')
                            setTimeout(() => {
                                setApp('none')
                                setLink2('none')
                                setLink1('/materalize')
                            }, 1500)
                        }
                    }}>Materalize
                    </div>
                </NavLink>

                <NavLink to='/'>
                    <div className='pureCss' onClick={() => {
                        if (link1 === '/materalize') {
                            setNav('reactLogoSmall')
                            setShow('not')
                            setApp('app')
                            setLink1('/')
                            setTimeout(() => {
                                setLink1('none')
                                setApp('none')
                                setLink2('/')
                            }, 1500)
                        }
                    }}>PureCss
                    </div>
                </NavLink>

                <div className='uzb' onClick={() => {
                    setLanguage('uzb')
                }}>UZB
                </div>
                <div className='rus' onClick={() => {
                    setLanguage('rus')
                }}>RUS
                </div>
                <div className='dvig'
                     onClick={() => {
                         setNav('reactLogoSmall')
                         setShow('not')
                     }}
                ><img src={close}/></div>
            </div>
            <div className={myImage}>
                <span onClick={showImage} className="close">×</span>
                <img src={srcMyImage} alt='my photo'/>
            </div>

        </div>
    );
}

export default App;
