import React, {useState} from "react";
import './materalize.css'
import 'materialize-css/dist/css/materialize.min.css'
import myPhoto from '../icon/myPhoto.jpg'

const Materalize = (props) => {
    const [leftBar, setleftBar] = useState([' col white-text  black s4 leftMaterial ', 'menu'])

    const Show = () => {
        if (leftBar[0] === 'mobile') {
            setleftBar([' col white-text  black s4 leftMaterial ', 'menu'])
        } else {
            setleftBar(['mobile', 'close'])
        }
    }
    return (
        <div className='container'>
            <div className='row'>
                <div className='col black s12'>
                    <div onClick={Show} className='menu'><i className='material-icons'>{leftBar[1]}</i></div>
                    <div className={leftBar[0]}>
                        <img onClick={props.showImage} id='image' className="circle responsive-img offset-s6 "
                             src={myPhoto}/>
                        <div>
                            <p className='ltitle '>{props.data.info}</p>
                            <div className='row'><span> <i
                                className="col  material-icons">event</i><b>{props.data.date}</b> </span></div>
                            <div className='row'><span> <i
                                className="col  material-icons">location_on</i><b>{props.data.location}</b> </span>
                            </div>
                        </div>
                        <div className='contact'>
                            <p className='ltitle '>{props.data.contact}</p>
                            <div className='row'>
                                <h6>
                                    <i className=" col material-icons">email</i><b>ismanov98q@mail.ru</b>
                                </h6>
                            </div>
                            <div className='row'><span> <i
                                className=" col material-icons">phone</i><b>+998998689884</b> </span></div>

                        </div>

                    </div>
                    <div className=' col white s8 '>

                        <div className="mini_container">
                            <h2 className="title">{props.data.fullName}</h2>
                            <h5 className="main-title">{props.data.profession}</h5>
                            <ul className="malumot">
                                <li className="timeline-blocks">
                                    <figure className="timeline-icon">
                                        <i className="material-icons small">school</i>
                                    </figure>
                                    <h6 className=' teg blue-text text-darken-2'>{props.data.edu}</h6>
                                    <div className="korobka ">
                                        <br/>
                                        <table className='shrift'>
                                            <tbody>
                                            <tr>
                                                <td>{props.data.year1}</td>
                                                <th>{props.data.colage}</th>
                                            </tr>


                                            <tr>
                                                <td>{props.data.year2}</td>
                                                <th>{props.data.univer}</th>
                                            </tr>

                                            </tbody>
                                        </table>
                                    </div>
                                </li>
                                <li className="timeline-blocks">
                                    <figure className="timeline-icon">
                                        <i className="material-icons small">work</i>
                                    </figure>
                                    <h6 className=' teg blue-text text-darken-2'>{props.data.job}</h6>
                                    <div className="korobka ">
                                        <br/>
                                        <table className='shrift'>
                                            <tbody>
                                            <tr>
                                                <td>{props.data.yearJob}</td>
                                                <th>{props.data.job1}</th>
                                            </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </li>
                                <li className="timeline-blocks">
                                    <figure className="timeline-icon">
                                        <i className="material-icons small">code</i>
                                    </figure>
                                    <h6 className=' teg blue-text text-darken-2'>{props.data.tech}</h6>
                                    <div className="korobka ">
                                        <table className='shrift'>
                                            <tbody>
                                            <tr>
                                                <td>{props.data.prlang}</td>
                                                <th>
                                                    JavaScript(ES6+)
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "85%"}}></div>
                                                    </p>
                                                    <p>TypeScript</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "60%"}}></div>
                                                    </p>
                                                    <p>Python(3.X)</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "85%"}}></div>
                                                    </p>
                                                    <p>Java</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "60%"}}></div>
                                                    </p>
                                                </th>
                                            </tr>
                                            <tr>
                                                <td>{props.data.frm}</td>
                                                <th>
                                                    <p>React</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "90%"}}></div>
                                                    </p>
                                                    <p>Redux(reduxThunk, reduxForm ...)</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "90%"}}></div>
                                                    </p>
                                                    <p>Express( Mongoose...)</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "46%"}}></div>
                                                    </p>
                                                    <p>Django</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "30%"}}></div>
                                                    </p>
                                                </th>
                                            </tr>
                                            <tr>
                                                <td>{props.data.dr}</td>
                                                <th>
                                                    <p>Nodejs</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "70%"}}></div>
                                                    </p>

                                                    <p>Webpack</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "65%"}}></div>
                                                    </p>
                                                    <p>Git</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "60%"}}></div>
                                                    </p>
                                                    <p>HTML</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "80%"}}></div>
                                                    </p>
                                                    <p>CSS</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "80%"}}></div>
                                                    </p>
                                                    <p>React-Bootstrap</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "70%"}}></div>
                                                    </p>
                                                    <p>MAteralizeCss</p>
                                                    <p className="progress">
                                                        <div className="determinate" style={{width: "70%"}}></div>
                                                    </p>

                                                    <p>...</p>
                                                </th>
                                            </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </li>
                            </ul>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default Materalize